<template>
  <div class="personal-content-body">
    <div class="table-content">
      <div class="clear table-content-title">
        <div class="left">信息发布</div>
        <el-input placeholder="搜索" v-model="searchText" class="header-right-time right" :clearable='true' @change="initData()">
          <i slot="prefix" class="el-input__icon el-icon-search "></i>
        </el-input>
      </div>
      <div class="table-group">
        <div class="table-group-content">
          <div class="clear table-group-title">
            <div class="left breadcrumb">
              <el-breadcrumb separator-class="el-icon-arrow-right">
                <el-breadcrumb-item :to="{name:'infoPublishIndex'}">信息发布</el-breadcrumb-item>
                <el-breadcrumb-item>项目舆情发布历史</el-breadcrumb-item>
              </el-breadcrumb>
            </div>
            <div class="right">
              <div class="table-group-title-btn btn-blue" @click="gotoCreate(-1,'new')">
                <span class="iconfont icon-add"></span>发布新舆情
              </div>
            </div>
            <div class="line"></div>
          </div>
          <div class="table-group-tab">
            <div class="table-group-tab-item" :class="{'current':type==0}" @click="changeTab(0)">数据采集</div>
            <div class="table-group-tab-item" :class="{'current':type==1}" @click="changeTab(1)">发布历史</div>
            <div class="clear"></div>
            <div class="line"></div>
          </div>
          <!-- pe数据列表 -->
          <div class="historyapply-group-tablebox table-group-tablebox" v-show="type==0">
            <div class="table-normal-box">
              <table class="table-normal">
                <tr>
                  <th width="30">序号</th>
                  <th>文章标题</th>
                  <th>公司</th>
                  <th width="80">来源</th>
                  <th>标签</th>
                  <th width="40">操作</th>
                </tr>
                <tr v-for="(item,index) in datalist_pe" :key="item.data_id">
                  <td>{{pageSize*(datalist_pe_currentPage-1)+index+1}}</td>
                  <td>{{item.title}}</td>
                  <td>{{item.name}}</td>
                  <td>{{item.source}}</td>
                  <td><span class="table-span" v-for="item1 in item.tag">{{item1}}</span></td>
                  <td>
                    <span class="table-link" @click="gotoCreate(item.data_id,'pe')">发布</span>
                  </td>
                </tr>
              </table>
              <div class="table-nodata" v-if="datalist_pe_total==0">
                <img src="../../images/table_nodata.png"> 没有对应数据
              </div>
            </div>
            <div class="pagination-normal" v-if="datalist_pe_total!=0">
              <el-pagination background layout="prev, pager, next" :total="datalist_pe_total" :page-size="pageSize" prev-text="上一页" next-text="下一页" @current-change="getlistdata" :current-page="datalist_pe_currentPage">
              </el-pagination>
            </div>
          </div>
          <!-- lp数据列表 -->
          <div class="historyapply-group-tablebox table-group-tablebox" v-show="type==1">
            <div class="table-normal-box">
              <table class="table-normal">
                <tr>
                  <th width="30">序号</th>
                  <th>文章标题</th>
                  <th>公司</th>
                  <!-- <th>来源</th> -->
                  <th>标签</th>
                  <th width="80">发布类型</th>
                  <th width="140">发布时间</th>
                  <th width="80">操作</th>
                </tr>
                <tr v-for="(item,index) in datalist_lp" :key="item.id">
                  <td>{{pageSize*(datalist_lp_currentPage-1)+index+1}}</td>
                  <td>{{item.title}}</td>
                  <td>{{item.name}}</td>
                  <!-- <td>{{item.source}}</td> -->
                  <td><span v-if="item1!=''" class="table-span" v-for="item1 in item.tag">{{item1}}</span></td>
                  <td>{{item.publish==1?'自主发布':'选择发布'}}</td>
                  <td>{{item.add_time}}</td>
                  <td>
                    <span class="table-link" @click="gotoCreate(item.id,'lp')">编辑</span>
                    <span class="table-link table-link-red" @click="gotoDelete(item.id)">删除</span>
                  </td>
                </tr>
              </table>
              <div class="table-nodata" v-if="datalist_lp_total==0">
                <img src="../../images/table_nodata.png"> 没有对应数据
              </div>
            </div>
            <div class="pagination-normal" v-if="datalist_lp_total!=0">
              <el-pagination background layout="prev, pager, next" :total="datalist_lp_total" :page-size="pageSize" prev-text="上一页" next-text="下一页" @current-change="getlistdatalp" :current-page="datalist_lp_currentPage">
              </el-pagination>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
import { peNewsList, lpPubHis, newsDelPubHis } from '../../config/api'
import { dialogConfirm, dialogConfirmHasbtn } from '../../config/mUtils'
export default {
  data() {
    return {
      type: 0, //0=>资讯列表1=>发布历史     
      company_id: '',
      pageSize: 10,
      searchText: '', //查询数据
      datalist_pe: [], //项目列表
      datalist_pe_total: 10,
      datalist_pe_currentPage: 1,
      datalist_lp: [], //项目列表
      datalist_lp_total: 10,
      datalist_lp_currentPage: 1,
    }
  },
  created() {
    if (this.$route.query.type != undefined) {
      this.type = 1;
    }
    this.initData();
  },
  computed: {},
  mounted() {},
  methods: {
    initData() {
      if (this.type == 0) {
        this.getPenewslist();
      } else {
        this.getlpPubHis();
      }
    },
    changeTab(id) {
      if (this.type != id) {
        this.type = id;
        this.datalist_pe_total = 0;
        this.datalist_pe_currentPage = 1;
        this.datalist_lp_total = 0;
        this.datalist_lp_currentPage = 1;
        this.searchText = '';
        this.initData();
      }
    },
    //获取pe app端资讯列表
    getPenewslist() {
      peNewsList({
        page: this.datalist_pe_currentPage,
        number: this.pageSize,
        search: this.searchText
      }).then(res => {
        if (res.data.msgNo == 1) {
          this.datalist_pe = res.data.payload.data;
          this.datalist_pe_total = res.data.payload.total;
        }
      })
    },
    //获取发布历史
    getlpPubHis() {
      lpPubHis({
        page: this.datalist_lp_currentPage,
        number: this.pageSize,
        //company_id:367,
        publish: '',
        search: this.searchText
      }).then(res => {
        if (res.data.msgNo == 1) {
          this.datalist_lp = res.data.payload.data;
          this.datalist_lp_total = res.data.payload.total;

        }
      })
    },
    getlistdata(val) {
      if (this.datalist_pe_total > this.pageSize) {
        this.datalist_pe_currentPage = val;
        this.initData();
      }
    },
    getlistdatalp(val) {
      if (this.datalist_lp_total > this.pageSize) {
        this.datalist_lp_currentPage = val;
        this.initData();
      }

    },
    //前往发布页面(type=>{new/pe/lp})
    gotoCreate(id, type) {
      this.$router.push({
        name: 'projectInfoPublish',
        query: {
          id: id,
          type: type
        }
      })
    },
    gotoDelete(id) {
      newsDelPubHis({
        id: id
      }).then(res => {
        if (res.data.msgNo == 1) {
          dialogConfirm('success', '删除成功');
        } else {
          dialogConfirm('fail', res.data.message);
        }
        this.initData()
      })
    }
  }
}

</script>
<style lang="scss" scoped>
@import '../../style/modal.scss';
.table-group-title-btn {
  &>span {
    display: inline-block;
    width: 20px;
    height: 20px;
  }
}

.peopleSelectTitle-text {
  font-size: 18px;
  color: #3C98E8;
  font-weight: bold;
  line-height: 40px;
  margin-right: 10px;
}

.peopleSelectTitle {
  border-bottom: 1px solid #dcdfe6;
}

.table-span {
  background: #E7F4FF;
  color: #3C98E8;
  font-size: 12px;
  margin: 2px;
  display: inline-block;
  float: left;
  padding: 0px 5px;
  line-height: 23px;
  max-width: 120px;
  overflow: hidden;
  text-overflow: ellipsis;
}

.table-normal {
  table-layout: fixed;
}

</style>



